<template>
	<div class="modal fade" id="lessonModal" tabindex="-1" role="dialog" aria-labelledby="contactModalTitle" aria-hidden="true">
		<div class="modal-dialog modal-lg" role="document">
			<div class="modal-content">
				<div class="modal-header modal__header">
					<h3 class="modal-title" id="contactModalTitle">Add Lesson</h3>
					<button type="button" class="close" data-dismiss="modal" aria-label="close">
						<span aria-hidden="true">&times;</span>
					</button>
				</div>
				<div class="modal-body modal__body">
					<form
						@submit.prevent="createLesson"
						class="modal__form"
						method="post">
						<div class="form-group">
							<label class="modal__label" for="class-title">Title</label>
							<input
								type="text"
								:class="[{'is-invalid': errors.title}]"
								class="form-control"
								id="class-title"
								placeholder="Class Title"
								@keydown="clearErrors($event.target.name)"
								v-model.trim="itemData.title">
							<div class="invalid-feedback">
								{{ errors.title }}
							</div>
						</div>
						<div class="form-group">
							<label class="modal__label" for="class-title">Description</label>
							<editor :init="init" ref="myTextEditor"
								v-model="itemData.description"
								:options="editorOption">
							</editor>
							<div class="invalid-feedback">
								{{ errors.description }}
							</div>
						</div>
					</form>
				</div>
				<div class="modal-footer modal__footer">
					<Loader v-if="loading"/>
					<button
						@click="clearErrors()"
						class="btn btn--md"
						type="button"
						data-dismiss="modal"
						aria-label="close"
						name="button">Cancel</button>
					<button
						@click.prevent="createLesson"
						class="btn btn--md btn__primary"
						type="submit"
						name="button">
						Save Lesson</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import 'quill/dist/quill.core.css';
import 'quill/dist/quill.snow.css';
import 'quill/dist/quill.bubble.css';
import { quillEditor } from 'vue-quill-editor';
import { createLesson } from '../../utils/api/classes';
import Loader from '../../components/Loader';
import form from '../../mixins/form';
import classes from '../../utils/validations/classes';
import { success, fail } from '../../utils/toast';
import eventHub from '../../utils/eventhub';
import init from '../../config/tinymce';

export default {
	name: 'AddLesson',
	mixins: [form],
	data() {
		return {
			init,
			itemData: {},
			loading: false,
			errors: {},
			editorOption: {
				modules: {
					toolbar: [
						['bold', 'italic', 'underline', 'strike'],
						['blockquote', 'code-block'],
						[{ 'header': 1 }, { 'header': 2 }],
						[{ 'list': 'ordered' }, { 'list': 'bullet' }],
						[{ 'script': 'sub' }, { 'script': 'super' }],
						[{ 'indent': '-1' }, { 'indent': '+1' }],
						[{ 'direction': 'rtl' }],
						[{ 'size': ['small', false, 'large', 'huge'] }],
						[{ 'header': [1, 2, 3, 4, 5, 6, false] }],
						[{ 'font': [] }],
						[{ 'color': [] }, { 'background': [] }],
						[{ 'align': [] }],
						['clean'],
						['link', 'image', 'video']
					]
				}
			}
		};
	},
	watch: {
		issues(val) {
			this.errors = { ...val };
		}
	},
	components: {
		Loader,
		quillEditor
	},
	methods: {
		createLesson() {
			this.validate(this.itemData, classes.lesson);
			if (Object.keys(this.issues).length) {
				this.errors = this.issues;
				return false;
			}
			this.loading = true;
			createLesson(this.$route.params.id, this.itemData)
				.then((response) => {
					if (response.status === 201) {
						this.loading = false;
						this.itemData = {};
						eventHub.$emit('lesson-created', response.data);
						success({
							title: 'Success! A new lesson has been created'
						});
					}
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		}
	},
	computed: {
		editor() {
			return this.$refs.myTextEditor.quill;
		}
	}
};
</script>

<style lang="scss" scoped>
input::placeholder {
	font-weight: normal;
}
.modal__form .form-control {
	height: 50px!important;
}
.modal__label {
	font-size: 14px!important;
}
.quill-editor {
    height: 20rem;
}
.modal__footer {
	margin-top: 7em;
}
</style>
