	uploadAvatar
} from '../../utils/api/classes';
import AddLesson from './AddLesson';
import Loader from '../../components/Loader';
import eventHub from '../../utils/eventhub';
import form from '../../mixins/form';
import { success, fail } from '../../utils/toast';
import classes from '../../utils/validations/classes';
import Pagination from '../../components/Pagination';
import AddQuiz from './AddQuiz';
import { getAclByKey } from '../../utils/api/acl';
import init from '../../config/tinymce';

export default {
	name: 'ClassDetail',
	mixins: [form],
	data() {
		return {
			init,
			loading: true,
			itemData: {},
			errors: {},
			learners: [],
			loadingLearners: true,
			learnersLoaded: false,
			limit: 6,
			offset: 0,
			total: 0,
			baseUrl: '',
			quizzes: [],
			loadingQuizzes: true,
			quizzesLoaded: false,
			updating: false,
			acls: [],
			avatar: '',
			avatarUploaded: false,
			isUploadingAvatar: false,
			editorOption: {
				modules: {
					toolbar: [
						['bold', 'italic', 'underline', 'strike'],
						['blockquote', 'code-block'],
						[{ 'header': 1 }, { 'header': 2 }],
						[{ 'list': 'ordered' }, { 'list': 'bullet' }],
						[{ 'script': 'sub' }, { 'script': 'super' }],
						[{ 'indent': '-1' }, { 'indent': '+1' }],
						[{ 'direction': 'rtl' }],
						[{ 'size': ['small', false, 'large', 'huge'] }],
						[{ 'header': [1, 2, 3, 4, 5, 6, false] }],
						[{ 'font': [] }],
						[{ 'color': [] }, { 'background': [] }],
						[{ 'align': [] }],
						['clean'],
						['link', 'image', 'video']
					]
				}
			}
		};
	},
	components: {
		AddLesson,
		Loader,
		Pagination,
		AddQuiz,
		quillEditor
	},
	created() {
		this.getClass();
		eventHub.$on('lesson-created', (payload) => {
			this.itemData.lessons.push(payload);
		});
		eventHub.$on('course-quiz-created', (payload) => {
			this.quizzes.unshift(payload);
		});
		this.limit = this.$route.query.limit ? this.$route.query.limit : this.limit;
		this.offset = this.$route.query.offset ? this.$route.query.offset : this.offset;
		this.baseUrl = `classes/${this.$route.params.id}/detail`;
		this.getClassLearners();
		this.getClassQuizzes();
	},
	mounted() {
		this.getAcl();
	},
	methods: {
		assignCourse() {
			assignCourse(this.$route.params.id, { user_id: this.itemData.user_id })
				.then((response) => {
					if (response.status === 201) {
						this.updating = false;
						success({
							title: 'Success! Course has been assigned!'
						});
						return true;
					}
					return false;
				})
				.catch((error) => {
					this.updating = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
		},
		getAcl() {
			getAclByKey('learning-content-manager')
				.then((response) => {
					if (response.status === 200) {
						this.acls = response.data.data.users;
						return true;
					}
					return false;
				})
				.catch((error) => {
					this.updating = false;
					return error;
				});
		},
		toggleActiveStatus() {
			const status = this.itemData.status === 0 ? 1 : 0;
			this.itemData.status = status;
			this.handleClassUpdate(this.$route.params.id, { status });
		},
		handleClassUpdate(id, data) {
			updateClass(id, data)
				.then((response) => {
					if (response.status === 200) {
						this.updating = false;
						success({
							title: 'Success! Course has been updated!'
						});
						return true;
					}
					return false;
				})
				.catch((error) => {
					this.updating = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		},
		deleteClass() {
			deleteClass(this.$route.params.id)
				.then((response) => {
					if (response.status === 200) {
						this.updating = false;
						success({
							title: 'Success! Course has been deleted!'
						});
						setTimeout(() => {
							this.$router.push('/classes');
						}, 500);
						return true;
					}
					return false;
				})
				.catch((error) => {
					this.updating = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
		},
		getClass() {
			getClass(this.$route.params.id)
				.then((response) => {
					if (response.status === 200) {
						this.itemData = response.data.data;
						this.loading = false;
					}
				})
				.catch((error) => {
					this.loading = false;
					return error;
				});
			return true;
		},
		updateClass() {
			const {
				title,
				description
			} = this.itemData;
			const result = {
				title,
				description,
				exam_attempts: this.itemData.exam_attempts,
				pass_grade: this.itemData.pass_grade,
				exam_duration: this.itemData.exam_duration,
				exam_instructions: this.itemData.exam_instructions,
				total_exam_question: parseInt(this.itemData.total_exam_question, 10)
			};

			this.validate(result, classes.update);
			if (Object.keys(this.issues).length) {
				this.errors = this.issues;
				return false;
			}

			this.updating = true;
			this.handleClassUpdate(this.$route.params.id, result);
			return true;
		},
		getClassLearners() {
			getClassLearners(this.$route.params.id, this.limit, this.offset)
				.then((response) => {
					if (response.status === 200) {
						this.learners = response.data.data;
						this.total = response.data.total;
						this.learnersLoaded = true;
						return true;
					}
					this.learnersLoaded = false;
					return false;
				})
				.catch((error) => {
					this.learnersLoaded = false;
					return error;
				})
				.finally(() => {
					this.loadingLearners = false;
				});
		},
		getClassQuizzes() {
			getClassQuizzes(this.$route.params.id)
				.then((response) => {
					if (response.status === 200) {
						this.quizzes = response.data.data;
						this.total = response.data.total;
						this.quizzesLoaded = true;
						return true;
					}
					this.quizzesLoaded = false;
					return false;
				})
				.catch((error) => {
					this.quizzesLoaded = false;
					return error;
				})
				.finally(() => {
					this.loadingQuizzes = false;
				});
		},
		deleteLesson(item) {
			deleteLesson(this.$route.params.id, item.id)
				.then((response) => {
					if (response.status === 200) {
						success({
							title: 'Success! Lesson has been deleted!'
						});
						this.itemData.lessons.splice(this.itemData.lessons.indexOf(item), 1);
						this.loading = false;
					}
				})
				.catch((error) => {
					fail({
						title: 'Oops! Something went wrong',
						text: error.data
					});
					this.loading = false;
					return error;
				});
			return true;
		},
		uploadAvatar(file) {
			this.isUploadingAvatar = true;
			this.avatarUploaded = false;
			uploadAvatar(this.$route.params.id, file)
				.then((response) => {
					if (response.status === 200) {
						this.itemData.avatar = file;
						this.avatarUploaded = true;
						return true;
					}
					this.avatarUploaded = false;
					return false;
				}).catch((error) => {
					this.avatarUploaded = false;
					return error;
				}).finally(() => {
					this.isUploadingAvatar = false;
				});
			return true;
		},
		changeAvatar() {
			const img = this.$refs.avatar.files[0];
			const imageSize = 3 * (2 ** 20);
			if (img && img.type.match(/image\/*/)) {
				if (img.size > imageSize) {
					errors.avatar = 'File size greater than 3MB';
					return false;
				}
			}
			const reader = new FileReader();
			reader.onload = (e) => {
				this.avatar = e.target.result;
				this.uploadAvatar(this.avatar);
			};
			reader.readAsDataURL(img);
			return true;
		}
	},
	watch: {
		$route() {
			this.limit = this.$route.query.limit ? this.$route.query.limit : 10;
			this.offset = this.$route.query.offset ? this.$route.query.offset : 0;
			this.getClassLearners();
		}
	},
	computed: {
		statusClass() {
			return this.itemData.status === 1 ? 'btn__danger' : '';
		},
		editor() {
			return this.$refs.myTextEditor.quill;
		}
	}
};
</script>


