<template>
	<div class="modal fade" id="quizModal" tabindex="-1" role="dialog" aria-labelledby="quizModalTitle" aria-hidden="true">
		<div class="modal-dialog" role="document">
			<div class="modal-content">
				<div class="modal-header modal__header">
					<h3 class="modal-title" id="questionModalTitle">Add Quiz</h3>
					<button type="button" class="close" data-dismiss="modal" aria-label="close">
						<span aria-hidden="true">&times;</span>
					</button>
				</div>
				<div class="modal-body modal__body">
					<form 
						@submit.prevent="createQuiz"
						class="modal__form" 
						method="post">
						<div class="form-group">
							<label class="modal__label" for="question">Quiz</label>
							<textarea 
								:class="[{'is-invalid': errors.question}]"
								class="form-control" 
								placeholder="Type a question" 
								id="question" 
								cols="30" 
								rows="10"
								@keydown="clearErrors($event.target.name)"
								v-model.trim="itemData.question"></textarea>
							<div class="invalid-feedback">
								{{ errors.question }}
							</div>
						</div>
						<div class="form-group">
							<label class="modal__label" for="option-a">Option A</label>
							<textarea 
								:class="[{'is-invalid': errors.option_a}]"
								class="form-control" 
								placeholder="Enter the first option" 
								id="option-a" 
								cols="30" 
								rows="10"
								@keydown="clearErrors($event.target.name)"
								v-model.trim="itemData.option_a"></textarea>
							<div class="invalid-feedback">
								{{ errors.option_a }}
							</div>
						</div>
						<div class="form-group">
							<label class="modal__label" for="option-b">Option B</label>
							<textarea 
								:class="[{'is-invalid': errors.option_b}]"
								class="form-control" 
								placeholder="Enter the second option" 
								id="option-b" 
								cols="30" 
								rows="10"
								@keydown="clearErrors($event.target.name)"
								v-model.trim="itemData.option_b"></textarea>
							<div class="invalid-feedback">
								{{ errors.option_b }}
							</div>
						</div>
						<div class="form-group">
							<label class="modal__label" for="option-c">Option C</label>
							<textarea 
								:class="[{'is-invalid': errors.option_c}]"
								class="form-control" 
								placeholder="Enter the third option" 
								id="option-c" 
								cols="30" 
								rows="10"
								@keydown="clearErrors($event.target.name)"
								v-model.trim="itemData.option_c"></textarea>
							<div class="invalid-feedback">
								{{ errors.option_c }}
							</div>
						</div>
						<div class="form-group">
							<label class="modal__label" for="option_d">Option D</label>
							<textarea 
								:class="[{'is-invalid': errors.option_d}]"
								class="form-control" 
								placeholder="Enter the last option" 
								id="option_d" 
								cols="30" 
								rows="10"
								@keydown="clearErrors($event.target.name)"
								v-model.trim="itemData.option_d"></textarea>
							<div class="invalid-feedback">
								{{ errors.option_d }}
							</div>
						</div>
						<div class="form-group">
							<label class="modal__label" for="category">What option is the Answer</label>
							<select 
								class="custom-select form-control"  
								id="category"
								:class="[{'is-invalid': errors.answer}]"
								v-model="option">
								<option value="option_a">Option A</option>
								<option value="option_b">Option B</option>
								<option value="option_c">Option C</option>
								<option value="option_d">Option D</option>
							</select>
							<div class="invalid-feedback">
								{{ errors.answer }}
							</div>
						</div>
					</form>
				</div>
				<div class="modal-footer modal__footer">
					<Loader v-if="loading"/>
					<button 
						@click="clearErrors()"
						class="btn btn--md" 
						type="button"
						data-dismiss="modal"
						aria-label="close"
						name="button">Cancel</button>
					<button 
						@click.prevent="createQuiz"
						class="btn btn--md btn__primary" 
						type="submit"
						name="button">
						Save Quiz</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { createQuiz } from '../../utils/api/classes';
import Loader from '../../components/Loader';
import eventHub from '../../utils/eventhub';
import form from '../../mixins/form';
import classes from '../../utils/validations/classes';
import { success, fail } from '../../utils/toast';

export default {
	name: 'AddQuiz',
	mixins: [form],
	data() {
		return {
			itemData: {
				option_a: '',
				option_b: '',
				option_c: '',
				option_d: ''
			},
			errors: {},
			loading: false,
			option: ''
		};
	},
	watch: {
		issues(val) {
			this.errors = { ...val };
		}
	},
	components: {
		Loader
	},
	methods: {
		createQuiz() {
			this.itemData.answer = this.option;
			this.validate(this.itemData, classes.quiz);
			if (Object.keys(this.issues).length) {
				this.errors = this.issues;
				return false;
			}
			this.itemData.online_class_id = this.$route.params.id;
			this.loading = true;
			createQuiz(this.$route.params.id, this.itemData)
				.then((response) => {
					if (response.status === 201) {
						this.loading = false;
						this.itemData = {};
						eventHub.$emit('course-quiz-created', response.data);
						success({
							title: 'Success! A new quiz has been created'
						});
					}
				})
				.catch(() => {
					this.loading = false;
					fail({
						title: 'Oops! Something went wrong. Pls try again.'
					});
				});
			return true;
		}
	}
};
</script>
